.info {
  padding: 120px 0 235px;

  @include vp-1199 {
    padding: 100px 0 183px;
  }

  @include vp-767 {
    padding: 56px 0 70px;
  }
}

.info .container {
  display: flex;
  justify-content: space-between;
  // gap: 9%;

  @include vp-1199 {
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }

  @include vp-767 {
    padding: 0;
    gap: 40px;
  }
}

.info__content {
  max-width: 436px;
  padding: 53px 27px 0 67px;

  @include vp-1199 {
    max-width: 502px;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include vp-767 {
      max-width: 100%;
      align-items: flex-start;
      padding: 0 15px 0;
    }

    & :last-child {
      display: none;
    }
  }

  & h2 {
    font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: 0.03em;
    font-family: $ff-alumni-sans;
    display: inline-flex;
    color: $color-sapphire;
    margin: 0 0 25px;

    @include vp-1199 {
      font-size: 42px;
      font-weight: 50px;
      margin: 0 0 6px;
    }

    @include vp-767 {
      font-size: 32px;
      font-weight: 38px;
      margin: 0 0 34px;
    }
  }

  & p {
    font-size: 16px;
    font-weight: 24px;
    color: $color-sapphire;
    margin: 0;
    font-weight: 300;

    @include vp-1199 {
      text-align: center;
    }

    @include vp-767 {
      text-align: left;
    }

    &:first-of-type {
      font-weight: 400;
      font-size: 18px;
      line-height: 27px;
      margin: 0 0 55px;

      @include vp-1199 {
        margin: 0 0 50px;
      }
    }
  }
}

.info__media {
  position: relative;
  padding-left: 63px;
  // height: 26.2vw;
  // max-width: 660px;
  // position: relative;

  @include vp-1199 {
    padding-left: 0;
    width: 100%;
    height: 100%;
  }

  @include vp-767 {
    min-width: 100%;
  }
}

.info__video-wrapper {
  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp-767 {
    gap: 40px;
  }
}

.info__video-inner {
  position: absolute;
  left: -19px;
  width: 50%;
  // max-width: 364px;
  // max-height: 228px;
  transform: translateY(128%);

  @include vp-1199 {
    width: 36%;
    height: auto;
    bottom: -20%;
    left: 33%;
    transform: none;
  }

  @include vp-767 {
    width: 100%;
    height: auto;
    position: relative;
    left: 0;
    top: 0;
    transform: none;
  }

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(20, 54, 122, 0.5);
    z-index: 2;
  }
}
